<template>
  <div style="display: inline-block">
    <button @click="doSomething($event)" :data-content="fdata">
      <slot></slot>
    </button>
  </div>
</template>

<script>
import html2canvas from "html2canvas";
export default {
  name: "fbutton",
  props: ["fdata", "sentEvent", "temp"],
  methods: {
    doSomething(e) {
      switch (this.sentEvent) {
        case "insert":
          this.insertThis(e);
          break;
        case "refresh":
          this.refreshEditables();
          break;
        case "reset":
          this.reset();
          break;
        case "download":
          this.download();
          break;

        default:
          break;
      }
    },
    insertThis(e) {
      let pos = document.querySelector("textarea").selectionStart;
      let currentText = document.querySelector("textarea").value;
      let toInsertText = e.currentTarget.dataset.content;
      document.querySelector("textarea").value =
        currentText.slice(0, pos) + toInsertText + currentText.slice(pos);
    },
    refreshEditables() {
      document
        .querySelectorAll("h1, h2, h3, p, mark, em, li, td")
        .forEach((editable) => {
          editable.contentEditable = "true";
        });
    },
    reset() {
      document.querySelector("textarea").value = this.temp;
    },
    download() {
      document.getElementById("main-square").style.cssText =
        "position: fixed; margin: 0; top: 0; left: 0";

      html2canvas(document.getElementById("main-square"), {
        x: 0,
        y: 0,
      })
        .then((canvas) => {
          let img = canvas.toDataURL("image/jpeg");
          saveAs(img, "myPost.jpeg");
        })
        .catch((err) => console.error(err));

      function saveAs(uri, filename) {
        var link = document.createElement("a");

        if (typeof link.download === "string") {
          link.href = uri;
          link.download = filename;

          //Firefox requires the link to be in the body
          document.body.appendChild(link);

          //simulate click
          link.click();

          //remove the link when done
          document.body.removeChild(link);
        } else {
          window.open(uri);
        }
      }
      document.getElementById("main-square").style.cssText =
        "position: relative; margin: 8px 0";
    },
  },
};
</script>

<style lang="scss" scoped>
button {
  font: bold 10px/1em farro;
  display: inline-block;
  border: none;
  outline: none;
  border-radius: 0px;
  padding: 14px;
  border-radius: 4px;
  background-color: var(--accent);
  color: var(--text);
  margin: 6px;
  cursor: pointer;
}
</style>
